/* Home.css */
.home {
  display: flex;            
  align-items: center;      
  justify-content: center;  
  height: 100vh;            
  text-align: center;       
  background-color: #f8f3f1; 
  color: #555; 
  transform: translateY(-70px);
}

.home-content {
  display: flex;
  flex-direction: column; 
  align-items: center;    
}

.profile-pic {
  width: 250px; 
  height: 250px; 
  border-radius: 50%; 
  object-fit: cover;
  margin-bottom: 20px; 
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); 
  animation: bounce 1s ease-out;
  transition: transform 0.3s;
  margin-top: -120px;
}

.home-text h1 {
  font-size: 2.5em;
  /* letter-spacing: 1px; */
  color: #333; 
  margin-bottom: -15px;
  margin-top: -25px;
}

.home-text h2 {
  font-size: 1.5em;
  font-weight: 400;
  color: #666; 
  /* margin-bottom: 20px; */
  margin-bottom: -10px; 
}

.home-text h3 {
  font-size: 1.5em;
  font-weight: 400;
  color: #333; 
  padding-top: 20px;
  padding-bottom: 0px;
  padding-left: 0px;
  margin-top: 0px;
  font-size: 1.5rem;
  line-height: 1.6;
  text-align: left; 
  max-width: 700px; 
  margin: 0 auto; 
}

.home-text ul {
  padding-top: 5px;
  padding-left: 50px;
  text-align: left; 
  font-size: 1.5rem;
  line-height: 1em;
  max-width: 700px; 
}

.bullet-text {
  color: #333;
}

/* .home-text i {
  /*color:#4A4E69;
  font-weight: 500;
} */

.home-text ul li {
  color: #666;
  padding-left: 170px;
  margin-bottom: 8px; 
  padding-left: 0px; 
  text-align: left; 
  font-size: 1.3rem;
  /* line-height: 1em; */
}

.home-text ul li a {
  color: #333;
  text-decoration: underline;
}

.home-text ul li a:hover {
    color: #E3879E;
    text-decoration: underline;
}


.home-text {
  animation: fadeIn 1.5s ease-in-out;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}